"use client";

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

const MotionDiv = motion.div;
const MotionButton = motion.button;

export default function ExtraButton() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div className="relative">
      <MotionButton
        onClick={() => setIsOpen(!isOpen)}
        className={`text-tiny text-muted hover:text-hover transition-colors duration-200 ${isOpen ? 'text-hover' : ''}`}
      >
        Extra {isOpen ? '−' : '+'}
      </MotionButton>
      
      <AnimatePresence>
        {isOpen && (
          <MotionDiv
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            transition={{ duration: 0.2 }}
            className="absolute left-0 top-8 bg-background py-2 min-w-[120px]"
          >
            <div className="space-y-2">
              <a 
                href="/uses" 
                className="block px-3 py-1 text-tiny text-muted hover:text-hover transition-colors duration-200"
              >
                Uses
              </a>
              <a 
                href="/books" 
                className="block px-3 py-1 text-tiny text-muted hover:text-hover transition-colors duration-200"
              >
                Books
              </a>
              <a 
                href="/setup" 
                className="block px-3 py-1 text-tiny text-muted hover:text-hover transition-colors duration-200"
              >
                Setup
              </a>
            </div>
          </MotionDiv>
        )}
      </AnimatePresence>
    </div>
  );
}
